<template>
    <!-- 搜索结果 -->
    <div class="app-result">
        <mt-header>
            <router-link to="search" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <!-- 内容 -->
            <ul class="result-ul">
                <li class="result-li" v-for="(item,i) of list" :key="i">
                    <router-link :to="item.href">
                        <div :data-rvid="item.isVid==1?'true':'false'" style='background:url("img/play.png") center center no-repeat'>
                            <img :src="item.pic" alt="">
                        </div>
                        <h3>{{item.title}}</h3>
                        <span style='background:url("img/eye_sm.png") no-repeat 77% center'>{{item.count}}</span>
                    </router-link> 
                </li>
            </ul>
    </div>
</template>
<script>
export default {
    data(){
        return{
            title:this.$route.query.title,
            list:[],
        }
    },
    created(){
        this.Sou();
    },
    methods:{
        Sou(){
            var url="sou?title="+this.title;
                this.axios.get(url).then(result=>{
                    this.list=result.data;   
            })
        }
    },
}
</script>
<style>
    /* 首页商品 */
    .result-ul{
        background: hsla(0, 4%, 87%, 0.59);
    }
    .result-ul .result-li img{
        height:180px;
    }
    .result-ul .result-li a{
         width:100%;
         height:100%;
    } 
    .result-ul .result-li{
        width:48%;
        display: inline-block;
        margin:15px 1%;
        background: rgb(255,255,255);
    }
        /* 视频播放图标 */
    .result-ul .result-li div {
        position: relative;
    }
    *[data-type=video]:after,*[data-rvid=true]:after{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .result-ul .result-li img{
        width:100%;
      
    }
    .result-ul .result-li h3{
        font-size:12px;
        text-align: center;
        color:rgb(36, 36, 36);
        margin-top:15px;
    }
    .result-ul .result-li span{
        color:rgba(0,0,0,0.64);
        width:92%;
        display: inline-block;
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 77% center ; */
        margin-top:5px;
        margin-bottom: 10px;
    }
</style>



