<template>
    <div class="app-home">
        <header class="header">
            <!-- 折叠导航 -->
            <mt-popup class="sharePopup" v-model="popupVisible" popup-transition="popup-fade">
                <ul class="shareUI">
                    <li v-for="(item,index) of arr" :key="index" @click="vdo(index)" :class="{active:tIndex===index}">{{item}}</li>
                </ul>
            </mt-popup>
            <!-- 折叠导航按钮 -->
            <div class="header-div">
                <span @click="btn"></span>
            </div>
            <!-- 搜索 -->
            <input v-model="sear" class="mint-button" placeholder="请输入">
            <mt-button @click="clickSear">搜索</mt-button>
        </header>
       
        <!-- 轮播图 -->
        
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,i) of navlist" :key=i>
                <img :src="item.img_url" :data-id="item.id" alt="" @click.prevent="jumpTo">
            </mt-swipe-item>
        </mt-swipe>
       
       
        <!-- 九宫格 -->
        <div class="special">
            <p>专题分类</p>
            <ul class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-li mui-table-view-cell mui-media mui-col-xs-4" v-for="(item,i) of gridlist" :key="i">
                     <router-link :to='"/productList?gid="+item.id'>
                        <img :src="item.pic" alt="">
                    </router-link>
                </li>
            </ul>
        </div>
        <!-- 首页商品 -->
        <div class="mui-index">
            <p>{{day}}推荐</p>
             <ul class="mui-ul">
                <li class="index-li" v-for="(item,i) of indexlist" :key="i">
                    <router-link :to='item.href'>
                        <div class="li-div">
                            <div :data-video="item.isVid==1?'true':'false'" style="background:url('img/play.png') center center no-repeat ;">
                            </div> 
                            <img :src="item.pic" alt="">
                        </div>
                       
                        <h3>{{item.title}}</h3>
                        <span style="background:url('img/eye_sm.png') no-repeat 77% center">{{item.count}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
       <Footer></Footer>
    </div>
</template>
<script>
import Footer from '@/components/Footer'
export default {
    components:{
        Footer
    },
    data(){
        return{
            day:"今日",
            popupVisible:false,
            // navbar:[],//隐藏菜单切换的内容
            navlist:[],//轮播图列表
            gridlist:[],//保存九宫格数组
            indexlist:[],//保存首页商品
            arr:["全部","文章","视频"],
            tIndex:0,
            sear:"",
        }
    },
    created(){
        this.navImage();
        this.handGrid();
        this.indexList();
    },
    methods:{
        // 轮播跳转
        jumpTo(e){
            var id=e.target.dataset.id;
            console.log(id)
            var path="/";
            if(id==1){
                path="video?lid=1"
            }
            this.$router.push(path);
        },
        vdo(index){
            this.tIndex=index;
            var everys=document.querySelector(".shareUI").children[index];
            var videos=document.querySelectorAll(".shareUI>li");
            if(everys.innerHTML=="全部"){
                this.day="今日"
                 var every=everys.innerHTML;
                var url="every?every="+every;
                this.axios.get(url).then(result=>{
                    this.indexlist=result.data;
                })
            }else{
                this.day=videos[index].innerHTML;
                var video=videos[index].innerHTML;
                var url="video?video="+video;
                this.axios.get(url).then(result=>{
                    this.indexlist=result.data;
                })
               
            }
        },  
        // 加载轮播图数据
        navImage(){
            // 1.发送ajax获取轮播图数据
            var url="imglist";
            this.axios.get(url).then(result=>{
                // 获取返回结果
                this.navlist=result.data.data;
                
            })
        },
        // 加载九宫格数据
        handGrid(){
            var url="grid";
            this.axios.get(url).then(result=>{
                this.gridlist=result.data;
            })
        },
        indexList(){
            var url="indexList";
            this.axios.get(url).then(result=>{
                this.indexlist=result.data;
            })
        },
        
        btn(){
            this.popupVisible=true  
         },
        // 弹出框当前标题高亮
        tu(index){
            this.tIndex=index;
        },
           jump(){
            var title=this.sear;
            var path="/";
            path="result?title="+title;
            this.$router.push(path);
        },
        clickSear(){
            var title=this.sear;
            var url="sousuo?title="+title;
            this.axios.get(url).then(result=>{
                this.jump();   
            })
        }
    }
}
</script>
<style>
    *{
        margin: 0;
        padding: 0;
    }
    ul{
        list-style:none;
        padding:0;
    }
    /* 组件mui */
    .mui-ui{
        padding-bottom:63px;
    }
    .app-home{
        background: #fff;
    }
    /* 头部 */
    .app-home .header{
        position:relative;
        width:100%;
        height:31px;
        vertical-align: middle;
    }
   
    .app-home .header .header-div{
        width:15%;
        height:27px;
        position:relative;
        display: inline-block;
        vertical-align: middle;
    }
    /*隐藏菜单 */
    .app-home .header .header-div>span{
        width: 30px;
        height: 5px;
        display: inline-block;
        position:absolute;
        top:7%;
        left:0;
        margin:0 17%;
        background: rgba(0, 0, 0, .59);
        
    }
    .app-home .header .header-div>span::before,.app-home .header .header-div>span::after{
        content:"";
        width: 30px;
        height: 5px;
        background: rgba(0, 0, 0, .59);
        display: inline-block;
        position: absolute;
    }
    .app-home .header .header-div>span::before{
        top:10px;
        left:0;
    }
     .app-home .header .header-div>span::after{
        top:20px;
        left:0;
    }
    /* 隐藏菜单弹出框 */
    .app-home .header .mint-popup{
        top:14%;
        left:20%;
        width: 76px;
        height: 100px;
        text-align: center;
        border-radius: 3px;
    }
    /* 弹出框背景颜色 */
    .app-home .header .v-modal{
        background: #fff;
        opacity:0;
    }
     .app-home .header .mint-popup .shareUI .active{
        color: #ff7800;
        border-bottom:1px solid #ff7800;
    }
    .app-home .header .mint-popup .shareUI{
        width:100%;
        height:100%;
        display:flex;
        flex-direction:column;
        justify-content: space-around;
    }
    .app-home .header .mint-popup .shareUI>li{
        border-bottom:1px solid #e2e2e2;

    }
    /* 搜索 */
    .app-container .mint-button{
       border:1px solid #c1c1c1;
       display: inline-block;
       margin:0;
       height:27px;
       vertical-align: middle;
    }
    .app-home input.mint-button{
       width: 65%;
       margin-right:5px; 
    }
    .app-home .mint-button--normal{
        padding: 0 7px;
    }
     .app-home .mint-swipe{
        height:200px;
    }
    .app-home .mint-swipe img{
        width:100%;
        height:100%;
    }
    /* 专题分类、今日推荐  */
    .app-home .special,.app-home .mui-index{
        width:100%;
    }
    .app-home .special p,.app-home .mui-index p{
        width:100%;
        height:40px;
        line-height: 44px;
        font-size: 22px;
        color:rgb(0,0,0);
        border-top:1px solid rgba(170,170,170,.5);
        margin-top:20px;    
    }
     /* 九宫格图片宽度 */
    .app-home .special .mui-grid-view.mui-grid-9 img{
        width: 93px;
        height: 93px;
    }
    /* 九宫格背景色  白色*/
    .app-home .special .mui-grid-view.mui-grid-9{
        background: rgb(255,255,255);
    }
    /* 九宫格ul的样式 */
     .app-home .special .mui-grid-view{
         padding:0;
         
     }
    .app-home special .mui-grid-view .mui-li a{
        display: inline-block;
    }
    /* 首页商品 */
    .app-home .mui-index{
        background: hsla(0, 4%, 87%, 0.59);
        
    }
    .app-home .mui-index .mui-index{
        padding-bottom:20px;
    }
    .app-home .mui-index .mui-ul .index-li:first-child img,.app-home .mui-index .mui-ul .index-li:nth-child(2) img{
        height:178px;
    }
     .app-home .mui-index .mui-ul .index-li:nth-child(3) img,.app-home .mui-index .mui-ul .index-li:nth-child(4) img{
        height:200px;
    }
     .app-home .mui-index .mui-ul .index-li:nth-child(7) img,.app-home .mui-index .mui-ul .index-li:nth-child(8) img{
        height:227px; 
    }
     .app-home .mui-index .mui-ul .index-li:nth-child(9) img,.app-home .mui-index .mui-ul .index-li:nth-child(10) img{
        height:267px; 
    }
     .app-home .mui-index .mui-ul .index-li:nth-child(13) img,.app-home .mui-index .mui-ul .index-li:nth-child(14) img{
        height:260px; 
    }
    .app-home .mui-index .mui-ul .index-li a{
         width:100%;
         height:100%;
    } 
    .app-home .mui-index .mui-ul .index-li{
        width:48%;
        display: inline-block;
        margin:7px 1%;
        background: rgb(255,255,255);
    }
    .app-home .mui-index .mui-ul .index-li .li-div{
        position:relative;
    }
    .app-home .mui-index .mui-ul .index-li img{
        width:100%;  
    }
    /* 视频播放图标 */
    *[data-video=true]{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .app-home .mui-index .mui-ul .index-li h3{
        font-size:12px;
        text-align: center;
        color:rgb(36, 36, 36);
        margin-top:15px;
    }
    .app-home .mui-index .mui-ul .index-li span{
        color:rgba(0,0,0,0.64);
        width:92%;
        display: inline-block;
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 77% center ; */
        margin-top:5px;
        margin-bottom: 10px;
    }
</style>




