<template>
    <div class="app-video">
            <!-- 页头 -->
            <mt-header>
                <router-link to="/" slot="left">
                    <mt-button icon="back">返回</mt-button>
                </router-link>
                <p slot="right">{{videos.title}}</p>
            </mt-header>
            <!-- 页面主体上 -->
            <div class="video-top">
                <div class="video-top-img">
                    <video controls :src="videos.mp4"></video>
                </div>
                <h4>{{videos.title}}</h4>
                <div class="video-re">
                    <p class="video-re-num">{{videos.classify}}</p>
                    <p>{{videos.dtime}}</p>
                </div>
                <div class="video-desc">
                    <p>{{videos.explains}}</p>
                </div>   
            </div>
            <div class="video-middle">
                <h2>{{videos.theme}}</h2>
                <div class="middle-content" v-html="videos.introduce">  
                </div>
            </div>
            <footer class="video-bottom">            
            <div class="mui-content">
                <div class="mui-content-padded">
                    <div class="flex-container">
                        <span id="icon-star"  :class="{active:Class.active}" @click="collect">
                            <span class="mui-icon mui-icon-star"></span>
                        </span>
                        <div class="bottom-right">
                        <span  class="">
                            <span class="mui-icon mui-icon-eye"></span>
                            {{videos.watch}}
                        </span>
                        <span @click="Likes"  :class="{active:isLikes.active}">
                            <span class="mui-icon-extra mui-icon-extra-heart"></span>
                            {{likesCount}}
                        </span>
                        <router-link id="icon-chatboxes" class="" :to='"comment?lid="+videos.lid'>
                            <span class="mui-icon mui-icon-chatboxes"></span>
                            {{Commentcount}}
                        </router-link>
                            </div>
                    </div>
                </div>
            </div>
            </footer>      
        <!-- </div> -->
    </div>
</template>
<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return{
            lid:this.$route.query.lid,//上个组件传递的id
            videos:{},//详情
            Commentcount:0,//评论量
            likesCount:0,//喜欢量
            Class:{
                active:false,
            },
            isLikes:{
                active:false,
            },
        }
    },
    created(){
        this.Video();
        this.Watch();
        this.getComment();
        this.getCollectLid(); 
        this.Own();
        this.likeCount();
       
    },
    methods:{
        // 页面详细内容
        Video(){
            var url="details?lid="+this.lid;
            this.axios.get(url).then(result=>{
                this.videos=result.data.data[0];
            })
        },
        // 收藏
        collect(){
            var title=this.videos.title;
            var pic=this.videos.pic;
            var watch=this.videos.watch;
            var isVideo=this.videos.isVideo;
            var classify=this.videos.classify;
             var url="cellection?lid="+this.lid+"&title="+title+"&pic="+pic+"&watch="+watch+"&isVideo="+isVideo+"&classify="+classify;
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    Toast("您尚未登录，请先登录");
                }else{
                    if(this.Class.active==false && result.data.code==1){
                        Toast("添加成功");
                        this.Class={
                            active:true
                        };
                    }else{
                        Toast("取消收藏")
                        this.Class={
                            active:false
                        };
                    } 
                }
            });
            
        },
         // 获取收藏
        getCollectLid(){
            var url="getCollectLid?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.data.length>0){
                    this.Class.active={
                        active:true
                    };
                }
            })
        },
        // 观看量
        Watch(){
            var url="Watch?lid="+this.lid;
            this.axios.get(url).then(result=>{
            })
        },
        // 喜欢量
        Likes(){
            var url="likes?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    Toast("您尚未登录，请先登录");
                }else if(this.isLikes.active==false){
                    this.likesCount++;
                    this.isLikes={
                        active:true
                    };
                }
            })
          
        },
        
        // 获取喜欢量
        likeCount(){
            var url="linkCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                 if(result.data.data.length>0){
                    this.likesCount=result.data.data[0].count;
                }
            })
        },
        // 获取喜欢量
        Own(){
            var url="Own?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.data.length>0){
                    this.isLikes={
                        active:true
                    };
                }
            })
        },
        // 评论量
        getComment(){
            var url="getCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.data.length>0){
                    this.Commentcount=result.data.data[0].count;
                }
            })
        },
    }
}
</script>
<style>
 
    /* 父元素样式 */
    .app-video{
        background: #e2e2e26e;
    }
    .app-video .video-top{
        width: 100%;
    }
    /* 顶部视频 */
    .app-video .video-top .video-top-img{
        width: 100%;
        background: #fff;
    }
    .app-video .video-top .video-top-img>video{
        width: 100%;
    }
    /* 标题 */
    .app-video .video-top>h4{
        text-align: center;
        padding-top:40px;
        color:#333333c7;
        background: #fff;
        margin: 0;
    }
    /* 分类名、发布时间 */
    .app-video .video-top .video-re{
        width: 100%;
        text-align: center;
        color:#777;
        padding-bottom:56px;
        background: #fff;
        padding-top:15px;
    }
    .app-video .video-top .video-re>p{
        margin-bottom:0;
        margin-top:5px; 

    }
    /* 方框 */
    .app-video .video-top .video-desc{
        width: 100%;
        padding:20px 0;
        background: #fff;
        margin-top: 13px;
        text-align:center;
    }
    .app-video .video-top .video-desc>p{
           padding:48px 0;
           border:1px solid #333;
    }
    /* 页面中间内容 */
    .app-video .video-middle{
        width: 100%;
        background: #fff;
    }
    .app-video .video-middle>h2{
        text-align: center;
        margin-top:13px;
        padding:30px 0;
        border-bottom:1px solid #e2e2e2;
        color:#333333c7;
    }
    .app-video .video-middle .middle-content{
        width: 100%;
    }
    .app-video .video-middle .middle-content p{
        color:#000;
    }
    /* 内容中p标签样式 */
    .app-video .video-middle .middle-content .cont-p{
        font-weight:550;
        font-size: 18px; 
    }
    /* 第一个p */
    .app-video .video-middle .middle-content .cont-mar{
        margin-top:30px; 
    }
    /* 第三个p */
    .app-video .video-middle .middle-content .cont-margin{
        margin-top:50px; 
    }
      /* 页尾 */
    .app-video .video-bottom{
        width: 100%;
        height:55px;
        position:fixed;
        top:92%;
        left:0;
        line-height: 55px;
        background: #fff;
    }
    .mui-content{
        background: #fff !important;
    }
    .flex-container>span:first-child{
        float: left;
        
    }
    .flex-container>a:not(first-child){
        float: right;
    }
    .flex-container .bottom-right{
        float: right;  
    }
    /* 点击收藏颜色变亮 */
    .active {
        color: #f56600;
    }
</style>



