<template>
    <!--  -->
    <div class="app-comment">
        <!-- 页头 -->
        <mt-header>
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">文章评论</p>
        </mt-header>
        <!-- 新闻评论 -->
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<textarea v-model="msg" placeholder="请吐槽最多120个字"></textarea>
                    <mt-button @click="addComment" size="large" type="primary">发表评论</mt-button>
				</div>
			</div>
		</div>
        <!-- 新闻发表评论 end-->
        <!-- 新闻评论列表 start -->
        <div class="mui-card" v-for="(item,i) of list" :key=i>
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <div class="cmt-list">
                        <div class="cmt-item" >
                            <div>第{{i+1}}楼 发表时间 ：{{item.ctime | datetimeFilter}}</div>
                            <div>评论内容：{{item.content}}</div>
                            <div class="zan">
                                <span ref="clickset" class="mui-icon-extra mui-icon-extra-like" @click.prevent="zan" :class="{active:isZan.active}" :data-id="item.id"></span>
                                <span >{{zanCound}}</span>
                            </div>
                        </div>
                    </div>
				</div>
			</div>
		</div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            msg:"",
            lid:this.$route.query.lid,
            list:[],//评论内容
            pno:0,//页码
            pageSize:5,//页大小
            zanCound:0,//点赞量
            isZan:{
                active:false,
            },
        }
    },
    created(){
        this.loadMore();
        this.getCount();
    },
    methods:{
        addComment(){
            // 1.评论内容双向绑定
            // 2.获取文章id，获取评论内容
            var content=this.msg;
            var lid=this.lid;
            // 3.去除评论两端空格
            var size=content.trim().length;
            //4.如果评论为空提示判断
            if(size==0){
                Toast("评论内容不能为空");
                return;
            }
            // 5.创建postData保存文章lid评论内容
            var postData=this.qs.stringify({
                lid:lid,
                content:content,
                
            })
            // 6.发送post请求
            var url="addcomment";
            this.axios.post(url,postData).then(result=>{
                // 7.评论成功或失败，提示
                if(result.data.code==1){
                    Toast("评论成功")
                }else{
                    Toast("您尚未登录，请先登录")
                }
                // 8.清空原有评论内容
                this.msg="";
                // 9.加载评论列表
                this.pno=0;//清空页码
                this.list=[];//清空原有数据
                this.loadMore();
                     
            });
            var lid=this.lid;
            var url="addCount?lid="+lid;
                this.axios.get(url).then(result=>{
            })
        },
        loadMore(){
            // 1.获取pno，pageSize
            this.pno++;
            var ps=this.pageSize;
            // 2.正则表达式验证pno和pageSize是否正确
            var reg=/^[0-9]{1,}$/;
            if(!reg.test(this.pno)){
                Toast("页码格式不正确");
                return;
            }
            var url="getComment?lid="+this.lid+"&pno="+this.pno;
            this.axios.get(url).then(result=>{
                this.list=result.data.data;
            });    
        },
        // 点赞
        zan(e){
            var cid=e.target.dataset.id;
            var url="zans?lid="+this.lid+"&cid="+cid;
            this.axios.get(url).then(result=>{
               if(result.data.code==-1){
                    Toast("您尚未登录，请先登录");
                }else{
                    if(this.isZan.active==false && result.data.code==1){
                        Toast("点赞成功");
                         this.isZan={
                            active:true
                        };
                       this.zanCound++;
                    }else{
                        Toast("取消点赞")
                         this.isZan={
                            active:false
                        };
                        this.zanCound--;
                    } 
                }
            })
            
        },
       
        // 获取点赞量
        getCount(){
            var url="zanCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                  if(result.data.data.length>0){
                    this.zanCound=result.data.data[0].count;
                    this.isZan.active={
                        active:true
                    };
                }
            })
        },
    },
}
</script>
<style>
    .mint-button-text{
        height: 100%;
    }
    .app-comment .mint-button{
        height:35px;
    }
    .cmt-item div{
        display: inline-block;
    }
    .zan{
        position:relative;
        left:47%;
        top:-20px;
       
    }
    .zan>span:last-child{
        margin-left:7px;
    }
</style>



