import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"

// 1.引入组件
import Home from "./components/home/Home.vue"
import ProductList from "./components/product/productList.vue"
import Details from "./components/product/details.vue"
import Video from "./components/product/video.vue"
import Login from './components/personal/login.vue'
import Personal from './components/personal/personal.vue'
import Recommend from './components/recommend/recommend.vue'
import Register from './components/personal/register.vue'
import Comment from './components/product/comment.vue'
import Collection from './components/personal/collection.vue'
import Search from './components/search/search.vue'
import Result from './components/search/result.vue'
import Set from './components/personal/set.vue'
import Text from './components/personal/text.vue'
import Pwd from './components/personal/pwd.vue'
import About from './components/personal/about.vue'
import News from './components/personal/news.vue'
import Fabulous from './components/personal/fabulous.vue'
Vue.use(Router)

export default new Router({
  // 为组件配置访问路径
  routes: [
    {path:'/',redirect:"/Home"},
    {path:'/Home',component:Home},
    {path:'/productList',component:ProductList},
    {path:'/details',component:Details},
    {path:'/video',component:Video},
    {path:'/login',component:Login},
    {path:'/personal',component:Personal},
    {path:'/recommend',component:Recommend},
    {path:'/register',component:Register},
    {path:'/comment',component:Comment},
    {path:'/collection',component:Collection},
    {path:'/search',component:Search},
    {path:'/result',component:Result},
    {path:'/set',component:Set},
    {path:'/text',component:Text},
    {path:'/pwd',component:Pwd},
    {path:'/about',component:About},
    {path:'/news',component:News},
    {path:'/fabulous',component:Fabulous},
  ]
})



