<template>
    <!-- 注册 -->
    <div class="app-register">
         <!-- 页头 -->
        <mt-header>
            <router-link to="login" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">注册</p>
        </mt-header>
        <!-- 注册框 -->
        <div class="login-box" style="background: url('/img/details/det-23/det-6.jpg') no-repeat">
            <div class="login-image">
                <log ref="myChild" @myParent="resRet" @getChild1="getVal1" @getChild2="getVal2" ></log>
                <div class="btn">
                    <span class="sText"></span>
                    <input type="button" value="注册" @click="Register">
                    <router-link to="login">已有账号登录</router-link>
                </div>   
            </div>
        </div>
    </div>
</template>
<script>
// 1.引入mint-ui Toast 局部使用
import {Toast} from "mint-ui"
import log from '@/components/log'
export default {
    components:{
        log
    },
     data(){
        return{
            childVal1:'',
            childVal2:'',
        }
        
    },
    methods:{
        // 检测手机号是否被注册
        resRet(){
            var phone1=this.childVal1;
            var url="retrieval?phone1="+phone1;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("该手机号已被注册");
                }
            })
        },
        // 跳转登录页
        jump(){
            this.$router.push("login")
        },
        // 获取子组件传来的值
        getVal1(data){
            this.childVal1=data;
        },
        getVal2(data){
            this.childVal2=data;
        },
        Register(){
            // 获取用户输入的手机号和密码
            var phone=this.childVal1;
            var upwd=this.childVal2;
            // 创建正则表达式验证手机号和密码格式是否正确
            var regP=/^1[3-8][0-9]{9}$/;
            if(!regP.test(phone)){
                Toast("手机号格式不正确，请检查");
                return;
            }
            var regU=/^\w{6,20}$/;
            if(!regU.test(upwd)){
                Toast("密码格式不正确，请检查");
                return;
            }
            var postData=this.qs.stringify({
                phone:phone,
                upwd:upwd,
            })
            var c=this.$refs.myChild.codeNumber();
            if(c==1){
                var url="register";
                this.axios.post(url,postData).then(result=>{
                    if(result.data==1 && c==1 && c!==""){
                        Toast("登陆成功,3秒后跳转登录页");
                        setTimeout(()=>{this.jump()},3000);
                    }else{
                        Toast("该手机号已被注册")
                    }
                })
            }else{
                Toast("验证码有误")
                this.$refs.myChild.createCode();
            }
        },
        
    },
}
</script>




