<template>
    <!-- 登录 -->
    <div class="app-login">
        <!-- 页头 -->
        <mt-header>
            <router-link to="personal" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">登录</p>
        </mt-header>
        <!-- 登录框 -->
        <div class="login-box" style="background: url('/img/details/det-23/det-6.jpg') no-repeat">
            <div class="login-image">
                <log ref="myChild" @getChild1="getVal1" @getChild2="getVal2" ></log>
                <div class="btn">
                    <span class="sText"></span>
                    <input type="button" value="登录" @click="loginClick">
                    <router-link to="register">注册账号</router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
// 1.引入mint-ui Toast 局部使用
import {Toast} from "mint-ui"
import log from '@/components/log'
export default {
    components:{
        log
    },
  
     data(){
        return{
            childVal1:'',
            childVal2:'',
        }
        
    },
    created(){
       
    },
    methods:{
        // 跳转首页
        jump(){
            this.$router.push("/Home")
        },
        // 获取子组件传来的值
        getVal1(data){
            this.childVal1=data;
        },
        getVal2(data){
            this.childVal2=data;
        },
        loginClick(){
            // 获取用户输入的手机号和密码
            var phone=this.childVal1;
            var upwd=this.childVal2;
            var postData=this.qs.stringify({
                phone:phone,
                upwd:upwd,
            })
            var c=this.$refs.myChild.codeNumber();
            if(c==1){
                var url="login";
                this.axios.post(url,postData).then(result=>{
                    if(result.data==1 && c==1 && c!==""){
                        Toast("登陆成功,3秒后自动跳转")
                        setTimeout(()=>{this.jump()},3000);
                        // 获取用户输入的用户名
                        var u=phone;
                        // 保存sesionStorage
                        sessionStorage.setItem("phone",u)
                    }else{
                        Toast("用户名或密码有误")
                        this.$refs.myChild.createCode();
                    }
                    
                })
            }else{
                Toast("验证码不正确，请检查");
            }
        },
        
    },
}
</script>
<style>
   
    
</style>



