<template>
    <!-- 修改密码-->
    <div class="app-pwd">
        <!-- 页头 -->
        <mt-header>
            <router-link to="set" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">修改密码</p>
        </mt-header>
        <!-- 内容 -->
        <div class="set-pwd">
            <div class="original">
                <span>原密码：</span>
                <input v-model="upwd" name="upwd" type="password" placeholder="旧密码">
            </div>
            <div class="new-pwd">
                <span>新密码：</span>
                <input v-model="upwd1" name="upwd1" type="password" placeholder="新密码">
                <input v-model="upwd2" name="upwd2" type="password" placeholder="确认新密码">
            </div>
            <div class="cancel">
                <p @click="clear">取消</p>
                <p @click="sub">确定</p>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            upwd:"",//原密码
            upwd1:"",//新密码
            upwd2:"",//确认新密码
        }
    },
    methods:{
        sub(){
            var upwd=this.upwd;
            var upwd1=this.upwd1;
            var reg=/^\w{6,20}$/;
            if(!reg.test(this.upwd1)){
                Toast("新密码格式不正确，请检查");
                return;
            }
            if(this.upwd1!=this.upwd2){
                Toast("两次密码输入不正确");
                return;
            }
            var postData=this.qs.stringify({
                upwd:upwd,
                upwd1:upwd1
            })
            var url="pwd?upwd="+upwd+"&upwd1="+this.upwd1;
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast("修改成功")
                    this.upwd="";
                    this.upwd1="";
                    this.upwd2="";
                }else{
                    Toast("原密码不正确")
                } 
            })
        },
        clear(){
            this.upwd="";
            this.upwd1="";
            this.upwd2="";
        },
    },
}
</script>
<style>
    /* 内容父元素 */
    .set-pwd{
        width: 85%;
        
        margin:0 auto;
        margin-top:40px;
    }
    /* 内容下所有span*/
    .set-pwd span{
        display:block;
        margin: 20px 0;
    }
    /* 内容下的所有input */
    .set-pwd input{
        width: 100% !important;
        border-radius:7px !important;
    }
    /* 取消，确定 */
    .cancel{
        width: 100%;
        display: inline-block;
        display: flex;
        justify-content: space-between;
        margin-top:20px;
    }
    .cancel>p{
        width: 31%;
        padding: 13px 0;
        text-align: center;
        color:#333;
        font-size:20px;
    }
    .cancel>p:first-child{
        background: #dcdcdc;
    }
    .cancel>p:last-child{
        background: #2ad181bd;
    }
</style>



