<template>
    <!--  个人中心 -->
    <div class="app-personal">
        <!-- 页头背景 -->
        <div class="bg" style="background:transparent url('img/bg-1.png') no-repeat center -55px;background-size:cover"></div>
        <!-- 登录 -->
        <div class="per-login">
            <img src="img/details/det-23/det-4.jpg" alt="">
            <div>
                <router-link  to="" @click.native="cLogin"  ref="clickLogin">点击登录</router-link>
            </div>
        </div>
        <!-- 九宫格 -->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
		    <li class="mui-table-view-cell mui-media mui-col-xs-4">
                <router-link to="news">
		            <img src="img/news.png" alt="">
		            <div class="mui-media-body">消息</div>
                </router-link>
            </li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4" >
                <router-link to="collection">
		            <img src="img/collect.png" alt="">
		            <div class="mui-media-body">收藏</div>
                </router-link>
            </li>
            <li class="mui-table-view-cell mui-media mui-col-xs-4" >
		        <div class="judge" @click="judge">
                <img src="img/set-up.png" alt="">
		        <div class="mui-media-body">设置</div></div>
            </li>
        </ul>
        <!-- 退出登录 -->
        <div>
            <div class="logout" @click="logout">退出登录</div>
        </div>
        <!-- 页尾 -->
        <Footer></Footer>
    </div>
</template>
<script>
import Footer from '@/components/Footer'
import {Toast} from 'mint-ui'
export default {
    components:{
        Footer
    },
    data(){
        return{
            personlist:[],
            phoneList:[],
        }
    },
    created(){
        this.person();
       
    },
    mounted(){
        this.display();
    },
    methods:{
        cLogin(){
            var name=this.$refs.clickLogin.$el;
            if(name.innerHTML=="立即登录"){
                this.$router.push('login');
            }
        },
        display(){
            var name=this.$refs.clickLogin.$el;
            var url="display";
            this.axios.get(url).then(result=>{
                this.phoneList=result.data.data[0];
                if(result.data.code!=-1){
                    name.innerHTML=this.phoneList.phone;
                    name.style.border=0;
                }else{
                    name.innerHTML="立即登录";
                }
            })
        },
        // 加载九宫格数据
        person(){
            var url="person";
            this.axios.get(url).then(result=>{
                 this.personlist=result.data;
            })
        },
        // 点击设置判断是否为登录状态
        judge(){
            var url="judge";
                this.axios.get(url).then(result=>{
                    if(result.data.code==-1){
                        Toast("您尚未登录，请先登录");
                    }else{
                        this.$router.push("/set");
                    } 
            })
        },
        // 退出登录
        logout(){
            var name=this.$refs.clickLogin.$el;
            var url="Logout?uid="+this.uid;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    name.innerHTML="立即登录";
                    name.style.border="1px solid #f56600";
                }
            })
        },
    }
}
</script>
<style>
    body{
        margin:0;
        padding:0;
    }
    .app-personal{
          overflow-x:hidden;
       overflow: hidden;
    }
    /* 背景父元素 */
    .bg{
        width: 100%;
        overflow: hidden;
        position: absolute;
        height: 190px;
    }
    
    /* 登录 */
    .per-login{
        width: 90%;
        height: 160px;
        margin: 100px auto 0;
        box-shadow: 0px 2px 5px rgba(0,0,0,.75);
        background: rgba(255,255,255);
        position: relative;
        top:-20px;
    }
    .per-login>img{
        width: 80px;
        height: 80px;
        margin:0 auto;
        margin-top:10px;
        display:block;
        position: relative;
        top:10px;
        background: #e2e2e2;
        border-radius:50%;
    }
    .per-login>div>a{
        display:block;
        width: 30%;
        margin:24px auto;
        text-align: center;
        line-height: 30px;
        color: rgb(245, 104, 9);
        border:1px solid rgb(245, 104, 9);
        border-radius:20px;
    }
      /* 九宫格图片宽度 */
    .mui-grid-9 img{
        width: 60px;
        height: 60px;
    }
    /* 九宫格背景色  白色*/
    .app-personal .mui-grid-view.mui-grid-9{
        background: #fff !important;
        margin-top:42px !important;
        box-shadow: 0px 1px 5px rgba(0,0,0,.75);
    }
    /* 设置 */
    .judge{
        padding: inherit;
    }
    /* 退出登录 */
    .logout{
        width: 90%;
        height:45px;
        background: rgba(170,170,170,.5);
        margin:70px auto;
        text-align: center;
        line-height:45px;
        border-radius:10px;
    }
</style>



