<template>
    <!-- 收藏 -->
    <div class="app-collection">
        <!-- 页头 -->
        <mt-header>
            <router-link to="personal" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">收藏</p>
        </mt-header>
        <!-- 内容 -->
        <ul class="mui-ul">
            <li class="index-li" v-for="(item,i) of collList" :key="i">
                <router-link :to='"details?lid="+item.flower_id'>
                    <div class="li-div">
                        <div :data-isVideo="item.isVid==1?'true':'false'" style="background:url('img/play.png') center center no-repeat ;">
                        </div> 
                        <img :src="item.pic" alt="">
                    </div>
                    <h3>{{item.title}}</h3>
                    <span class="li-span">#{{item.classify}}#</span>
                    <span style='background:url("img/eye_sm.png") no-repeat 43% center'>{{item.watch}}</span>
                </router-link> 
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
        return{
            collList:'',
        }
    },
    created(){
        this.getCollect();
    },
    methods:{
        getCollect(){
            var url="getCollect";
            this.axios.get(url).then(result=>{
                this.collList=result.data.data;
            })
        }
    }
}
</script>
<style>

     .app-collection .mui-ul{
        background: #e2e2e261;
    }
     .app-collection .mui-ul .index-li img{
        height:250px;
    }
    .app-collection .mui-ul .index-li a{
         width:100%;
         height:100%;
    } 
    .app-collection .mui-ul .index-li{
        width:48%;
        display: inline-block;
        margin:15px 1%;
        background: #fff;
    }
        /* 视频播放图标 */
    .app-collection .mui-ul .index-li .li-div{
        position: relative;
    }
    *[data-isVideo=true]{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .app-collection .mui-ul .index-li img{
        width:100%;
    }
    .app-collection .mui-ul .index-li h3{
        font-size:12px;
        text-align: center;
        color:#333;
        margin-top:15px;
    }
    .app-collection .mui-ul .index-li span{
        color:#00000087;
        width:50%;
        font-size: 16px;
        display: inline-block;
        margin-top:5px;
        margin-bottom: 10px;
    }
    .app-collection .mui-ul .index-li .li-span{
        text-align: left; 
    }
    .app-collection .mui-ul .index-li span:last-child{
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 43% center ; */
    }
</style>



