<template>
    <!-- 推荐 -->
    <div class="app-recommend">
         <!-- 页头 -->
        <mt-header>
            <p slot="right">推荐</p>
        </mt-header>
        <!-- 内容 -->
            <ul class="mui-ul">
                <li class="index-li" v-for="(item,i) of list" :key="i">
                    <router-link :to="item.href">
                        <div class="li-div">
                            <div :data-vid="item.isVid==1?'true':'false'" style="background:url('img/play.png') center center no-repeat ;">
                            </div> 
                            <img :src="item.pic" alt="">
                        </div>
                        <h3>{{item.title}}</h3>
                        <span style='background:url("img/eye_sm.png") no-repeat 77% center'>{{item.count}}</span>
                    </router-link> 
                </li>
            </ul>
            <!-- 页尾 -->
            <Footer></Footer>
    </div>
</template>
<script>
import Footer from '@/components/Footer'
export default {
    components:{
        Footer
    },
    data(){
        return{
            list:[],
        }
    },
    created(){
        this.recommend();
    },
    methods:{
         recommend(){
            var url="recommend";
            this.axios.get(url).then(result=>{
                this.list=result.data;
            })
        },
    },
}
</script>
<style>
    .mui-ul{
        background: hsla(0, 4%, 87%, 0.59);
        padding-bottom: 63px;
    }
    .mui-ul .index-li img{
        height: 250px;
    }
    .mui-ul .index-li a{
         width:100%;
         height:100%;
    } 
    .mui-ul .index-li{
        width:48%;
        display: inline-block;
        margin:15px 1%;
        background: #fff;
    }
        /* 视频播放图标 */
    .mui-ul .index-li .li-div {
        position: relative;
    }
    *[data-vid=true]{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .mui-ul .index-li img{
        width:100%;
      
    }
    .mui-ul .index-li h3{
        font-size:12px;
        text-align: center;
        color:rgb(36, 36, 36);
        margin-top:15px;
    }
    .mui-ul .index-li span{
        color:rgba(0,0,0,0.64);
        width:92%;
        display: inline-block;
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 77% center ; */
        margin-top:5px;
        margin-bottom: 10px;
    }
</style>




