<template>
    <!-- 九宫格详情表 -->
    <div class="app-list">
        <mt-header>
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right" v-for="(item,i) of title[gid-1]" :key="i">{{item}}</p>
        </mt-header>
        <!-- 商品介绍 -->
        <div class="mitn-list">
            <span :class="{active:tIndex===index}" @click="tu(index)" v-for="(item,index) of arr[gid-1]" :key="index">{{item}}</span>
            <!-- 内容 -->
            <ul class="mui-ul">
                <li class="index-li" v-for="(item,i) of list" :key="i">
                    <router-link :to="item.href">
                        <div class="li-div">
                            <div :data-vid="item.isVid==1?'true':'false'" style="background:url('img/play.png') center center no-repeat ;">
                            </div> 
                            <img :src="item.pic" alt="">
                        </div>
                        <h3>{{item.title}}</h3>
                        <span style='background:url("img/eye_sm.png") no-repeat 77% center'>{{item.count}}</span>
                    </router-link> 
                </li>
            </ul>
            
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            gid:this.$route.query.gid,//上一个组件传递id--九宫格
            list:[],//标题内容
            title:[//大标题
                ["花艺学堂"],
                ["园艺花园"],
                ["家居配搭"],
                ["婚嫁"],
                ["植物百科"],
                ["诗意生活"],
            ],
            arr:[//标题
                ["最新","中式","西式"],
                ["最新","室内搭配","户外种植"],
                ["最新","植物搭配","家居软装"],
                ["最新","攻略","西式"],
                ["最新","多肉","观叶植物"],
                ["最新","芬芳料理","绘画"],
            ],
            tIndex:0,
        }
    },
    created(){
        this.ProductList();
    },
    methods:{
         // 当组件创建成功后加载当前标题内容
        ProductList(){
            var url="productList?gid="+this.gid;
            this.axios.get(url).then(result=>{
                this.list=result.data;
            })
        },
        tu(index){
            this.tIndex=index;
            var znames=document.querySelector(".mitn-list").children[index];
            var hnames=document.querySelectorAll(".mitn-list>span");
            if(znames.innerHTML=='最新'){
                var zname=znames.innerHTML;
                var url="zuixin?gid="+this.gid+"&zname="+zname;
                this.axios.get(url).then(result=>{
                    this.list=result.data;
                     // 评论人数+1
                this.$store.commit("watch")
                })
            }else{
                var hname=hnames[index].innerHTML;
                var url="sousuo?gid="+this.gid+"&hname="+hname;
                this.axios.get(url).then(result=>{
                    this.list=result.data;
                })
            }
        },     
    }
}
</script>
<style>
    
      ul{
        list-style:none;
        padding:0;
    }
   
    /* 轮播 */
    .app-details .mint-swipe{
        height: 375px;
    }
    .app-details .mint-swipe img{
        width:100%;
        height:100%;
    }
   
    /* 标题 */
    .app-list .mitn-list>p{
        text-align:center;
        font-size:22px;
        height:60px;
        line-height:60px;
        color:#000;
        
    }
    /* 标题下的导航 */
    .app-list .mitn-list>span{
        display: inline-block;
        width: 33%;
        height:30px;
        color:#0009;
        margin-top:15px;
        text-align: center;
        border-bottom:1px solid #e2e2e2;
        margin-bottom: 20px;
    }
    .app-list .mitn-list .active{
        color: #ff7800;
        border-bottom:1px solid #ff7800;

    }
    /* 商品简介图片 */
    .app-list .picture{
        width: 100%;
    }
     .app-list .picture div img{
        width: 100%;
    }
     /* 首页商品 */
    .app-list .mitn-list .mui-ul{
        background: #e2e2e261;
    }
    .app-list .mitn-list .mui-ul .index-li:first-child img,.app-list .mitn-list .mui-ul .index-li:nth-child(2) img{
        height:230px;
    }
     .app-list .mitn-list .mui-ul .index-li:nth-child(3) img,.app-list .mitn-list .mui-ul .index-li:nth-child(4) img{
        height:270px;
    }
    .app-list .mitn-list .mui-index .mui-ul .index-li a{
         width:100%;
         height:100%;
    } 
    .app-list .mitn-list .mui-ul .index-li{
        width:48%;
        display: inline-block;
        margin:15px 1%;
        background: #fff;
    }
        /* 视频播放图标 */
    .app-list .mitn-list .mui-ul .index-li .li-div{
        position: relative;
    }
    *[data-vid=true]{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .app-list .mitn-list .mui-ul .index-li img{
        width:100%;
      
    }
    .app-list .mitn-list .mui-ul .index-li h3{
        font-size:12px;
        text-align: center;
        color:#333;
        margin-top:15px;
    }
    .app-list .mitn-list .mui-ul .index-li span{
        color:#00000087;
        width:92%;
        display: inline-block;
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 77% center ; */
        margin-top:5px;
        margin-bottom: 10px;
    }
</style>



