<template>
    <!-- 搜索 -->
    <div class="app-search">
        <div class="search">
             <!-- 页头 -->
        <mt-header>
            <p slot="right">搜索</p>
        </mt-header>
        <div class="mint-search">
            <i class="mintui mintui-search"></i>
          <input  v-model="value"  placeholder="请输入"> 
          <input  @click="Sousuo" placeholder="搜索"> 
        </div>
        </div>
        <!-- 内容 -->
        <div class="content">
            <div class="content-left">
                 <ul class="left-ul">
                <li v-for="(item,index) of str" :key="index" @click="vdo(index)" :class="{active:tIndex===index}">{{item}}</li>
            </ul>
            </div>
            <div class="content-right">
                <ul class="right-ul">
                    <li class="right-li" v-for="(item,i) of list" :key="i">
                        <router-link :to="item.href">
                            <div class="li-div">
                            <div :data-vid="item.isVid==1?'true':'false'" style="background:url('img/play.png') center center no-repeat ;">
                            </div> 
                            <img :src="item.pic" alt="">
                        </div>
                            <h3>{{item.title}}</h3>
                            <span style='background:url("img/eye_sm.png") no-repeat 77% center'>{{item.count}}</span>
                        </router-link> 
                    </li>
            </ul>
            </div>
        </div>
        <!-- 页尾 -->
        <Footer></Footer>
    </div>
</template>
<script>
import Footer from '@/components/Footer'
export default {
    components:{
        Footer
    },
    data(){
        return{
            str:["最新","文章","视频","婚嫁","花艺学堂","园艺花园","家居搭配","植物百科","诗意生活"],
            tIndex:0,
            list:[],
            value:'',
        }
    },
    created(){
        this.searth();
        
    },
    methods:{
          searth(){
            var url="recommend";
            this.axios.get(url).then(result=>{
                this.list=result.data;
            })
        },
        vdo(index){
            this.tIndex=index;
            var znames=document.querySelector(".left-ul").children[index];
            var videos=document.querySelectorAll(".left-ul>li");
            if(this.tIndex==0){
                 var zname=znames.innerHTML;
                var url="quan?zname="+zname;
                this.axios.get(url).then(result=>{
                    this.list=result.data;
                })
            }else if(this.tIndex==1 || this.tIndex==2){
                var video=videos[index].innerHTML;
                var url="video?video="+video;
                this.axios.get(url).then(result=>{
                    this.list=result.data;
                })  
            }else{
                var ti=videos[index].innerHTML;
                var url="biaoTi?ti="+ti;
                this.axios.get(url).then(result=>{
                    this.list=result.data;
                })
            }
        },
        
        jump(){
            var title=this.value;
            var path="/";
            path="result?title="+title;
            this.$router.push(path);
        },
        Sousuo(){
            var title=this.value;
            var url="sousuo?title="+title;
                this.axios.get(url).then(result=>{
                    this.jump();
                   
            })
        }

    },
}
</script>
<style>
    body{
        height: 100%;
    }
    .search{
        position: fixed;
        top:0;
        z-index: 10000;
        width: 100%;
        background: rgb(255,255,255);
    }
    /* 头部 */
    .mint-header{
        position: relative;
        z-index: 333;
    }
    .mint-header .mint-header-button.is-right{
        flex:0.45 !important;
    }
    /* 搜索框最外层父元素 */
       .mint-search{ 
        width: 95%;
        height:30px !important;
        position: relative;
        z-index: 333;
        margin:5px 3% 6px;
        border-radius: 6px;
        background-color:hsla(0,0%,75%,.5) !important;
    }
    /* 搜索图标 */
    .mintui-search:before {
        content: "\E604";
    }
    /* 搜索框 */
    .mint-search>input:nth-child(2){
        width: 84%;
        font-size: 16px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        height: 30px !important;
        text-align: center;
        border: 0;
        border-radius: 6px;
        padding:0 0!important;
        margin-bottom:0 !important;
        background: hsla(0, 0%, 75%, 0.05) !important; 
    } 
    /* 搜索按钮 */
    .mint-search>input:last-child{
        width: 12%;
        border-radius: 6px;
        height: 30px;
        position: absolute;
        top:0;
        right:0;
        border:0;
        outline: none;
        text-align: center;
        background:  rgba(167, 167, 167, 0.5);
        
    }
   
    /* 搜索图标 */
    .mintui-search{
        font-size: 16px !important;
        color:#5c5757 !important;
    }
    

    /* 内容 */
    .content{
        width: 100%;
        height: 100%;
        margin-top:101px ;
    }
    .content:after{
        content:'';
        display: block;
        clear:both;
    }
    /* 内容左边的标题 */
    .content .content-left{
        position: fixed;
        width: 20%;
        float: left;
        height: 100%;
    }
    .left-ul{
        height:76%;    
        text-align: center;
        display: flex;
        flex-direction:column;
         overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        justify-content:space-around; 

    }
    /* 标题右边的内容 */
    .content .content-right{
        width: 79%;
        float: right;
    }
    .right-ul{
        background: hsla(0, 4%, 87%, 0.59);
        width: 100%;
        padding-bottom:63px;
    }
    .right-ul .right-li img{
        height: 170px;
    }
    .right-ul .right-li a{
         width:100%;
         height:100%;
    } 
    .right-ul .right-li{
        width:48%;
        display: inline-block;
        margin:15px 1%;
        background: rgb(255,255,255);
        
    }
        /* 视频播放图标 */
    .right-ul .right-li .li-div{
        position: relative;
    }
    *[data-vid=true]{
        content:"";
        width: 56px;
        height: 56px;
        display:block;
        position:absolute;
        /* background:url("img/play.png") center center no-repeat ; */
        top:37%;
        left:37%;
    }
    .right-ul .right-li img{
        width:100%;
      
    }
    .right-ul .right-li h3{
        font-size:1px;
        text-align: center;
        color:rgb(36, 36, 36);
        margin-top:15px;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap
    }
    .right-ul .right-li span{
        color:rgba(0,0,0,0.64);
        width:92%;
        display: inline-block;
        text-align: right;
        /* background:url("img/eye_sm.png") no-repeat 77% center ; */
        margin-top:5px;
        margin-bottom: 10px;
    }
</style>



