<template>
    <!-- 修改密码-->
    <div class="app-text">
        <!-- 页头 -->
        <mt-header>
            <router-link to="set" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">修改密码</p>
        </mt-header>
        <!-- 内容 -->
        <div class="set-text">
            <div class="original">
                <span>原手机号：</span>
                <input v-model="phone" name="phone" type="password" placeholder="原手机号">
            </div>
            <div class="new-text">
                <span>新手机号：</span>
                <input v-model="phone1" name="phone1" type="password" placeholder="新手机号" @blur="textRet">
                <input v-model="phone2" name="phone2" type="password" placeholder="确认新手机号">
            </div>
            <div class="cancelT">
                <p @click="clearT">取消</p>
                <p @click="subT">确定</p>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            phone:"",//原手机号
            phone1:"",//新手机号
            phone2:"",//确认新手机号
        }
    },
    methods:{
        // 检测手机号是否被注册
        textRet(){
            var url="retrieval?phone1="+this.phone1;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("该手机号已被注册");
                    this.phone1="";
                }
            })
        },
        subT(){
            var phone=this.phone;
            var phone1=this.phone1;
            var reg=/^1[3-8][0-9]{9}$/;
            if(!reg.test(this.phone1)){
                Toast("手机号格式不正确，请检查");
                return;
            }
            if(this.phone1!=this.phone2){
                Toast("两次手机号输入不正确");
                return;
            }
            var postData=this.qs.stringify({
                phone:phone,
                phone1:phone1
            })
            var url="text?phone="+phone+"&phone1="+this.phone1;
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast("修改成功");
                    this.phone="";
                    this.phone1="";
                    this.phone2="";  
                }else{
                    Toast("原手机号不正确")
                } 
            })
        },
        clearT(){
            this.phone="";
            this.phone1="";
            this.phone2="";
        },
    },
}
</script>
<style>
    /* 内容父元素 */
    .set-text{
        width: 85%;
        margin:0 auto;
        margin-top:40px;
    }
    /* 内容下所有span*/
    .set-text span{
        display:block;
        margin: 20px 0;
    }
    /* 内容下的所有input */
    .set-text input{
        width: 100% !important;
        border-radius:7px !important;
    }
    /* 取消，确定 */
    .cancelT{
        width: 100%;
        display: inline-block;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top:20px;
    }
    .cancelT>p{
        width: 31%;
        padding: 13px 0;
        text-align: center;
        color:#333;
        font-size:20px;
    }
    .cancelT>p:first-child{
        background: #dcdcdc;
    }
    .cancelT>p:last-child{
        background: #2ad181bd;
    }
</style>



