<template>
    <div class="app-details" >
        <!-- 页头 -->
        <mt-header>
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <p slot="right">{{detailList.title}}</p>
        </mt-header>
        <!-- 页面上半部分 -->
        <div class="details-top">
            <div class="details-top-img">
                <img :src="detailList.mp4" alt="">
            </div>
            <h4>{{detailList.title}}</h4>
            <div class="details-re">
                <p class="details-re-num">{{detailList.classify}}</p>
                <p>{{detailList.dtime}}</p>
            </div>
            <div class="details-desc">
                <p>{{detailList.explains}}</p>
            </div>
            <div class="details-content">
                <p>{{detailList.detailed}}</p>
            </div>
        </div>
        <!-- 页面主体内容 -->
        <div class="details-middle">
            <h2>{{detailList.theme}}</h2>
            <div class="middle-content" v-html="detailList.introduce">
            </div>
        </div>
        <!-- 页尾 -->
        <footer class="details-bottom">
            <div class="mui-content">
                <div class="mui-content-padded">
                    <div class="flex-container">
                        <span id="icon-star"  :class="{active:Class.active}" @click="collect">
                            <span class="mui-icon mui-icon-star"></span>
                        </span>
                        <div class="bottom-right">
                        <span  class="">
                            <span class="mui-icon mui-icon-eye"></span>
                            {{detailList.watch}}
                        </span>
                        <span @click="DLikes"  :class="{active:isLikes.active}">
                            <span class="mui-icon-extra mui-icon-extra-heart"></span>
                             {{DlikesCount}}
                        </span>
                        <router-link id="icon-chatboxes" class="" :to='"comment?lid="+detailList.lid'>
                            <span class="mui-icon mui-icon-chatboxes"></span>
                            {{Commentcount}}
                        </router-link>
                            </div>
                    </div>
                </div>
            </div>     
        </footer>
    </div>
</template>
<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return{
            lid:this.$route.query.lid,//上个组件传递的lid
            detailList:{},//详情
            Commentcount:0,//评论量
            DlikesCount:0,//喜欢量
            Class:{
                active:false,
            },
            isLikes:{
                active:false,
            },
        }
    },
    created(){
        this.productDetails();
        this.Watch();
        this.getComment();
        this.getCollectLid();
         this.DOwn();
        this.DlikeCount();
    },
    methods:{
        // 详情
        productDetails(){
            var url="details?lid="+this.lid;
            this.axios.get(url).then(result=>{
                this.detailList=result.data.data[0];
            })
        },
        // 收藏
        collect(){
            var title=this.detailList.title;
            var pic=this.detailList.pic;
            var watch=this.detailList.watch;
            var isVideo=this.detailList.isVideo;
            var classify=this.detailList.classify;
            var url="cellection?lid="+this.lid+"&title="+title+"&pic="+pic+"&watch="+watch+"&isVideo="+isVideo+"&classify="+classify;
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    Toast("您尚未登录，请先登录");
                }else{
                    if(this.Class.active==false && result.data.code==1){
                        Toast("添加成功");
                        this.Class={
                            active:true
                        };
                    }else{
                        Toast("取消收藏")
                        this.Class={
                            active:false
                        };
                    } 
                }
            });
            
        },
         // 获取收藏
        getCollectLid(){
            var url="getCollectLid?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.data.length>0){
                    this.Class.active={
                        active:true
                    };
                }
            })
        },
        // 观看量
        Watch(){
            var url="Watch?lid="+this.lid;
            this.axios.get(url).then(result=>{
            })
        },
         // 点击喜欢量
        DLikes(){
            var url="likes?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    Toast("您尚未登录，请先登录");
                }else if(this.isLikes.active==false){
                    this.DlikesCount++;
                    this.isLikes={
                        active:true
                    };
                }
            })
          
        },
        
        // 获取喜欢量
        DlikeCount(){
            var url="linkCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                 if(result.data.data.length>0){
                    this.DlikesCount=result.data.data[0].count;
                }
            })
        },
        // 获取喜欢量
        DOwn(){
            var url="Own?lid="+this.lid;
            this.axios.get(url).then(result=>{
                 if(result.data.data.length>0){
                    this.isLikes={
                        active:true
                    };
                }
            })
        },
        // 评论量
        getComment(){
            var url="getCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                if(result.data.data.length>0){
                    this.Commentcount=result.data.data[0].count;
                }
            })
        },
    }
}
</script>
<style>
    
    /* 父元素样式 */
    .app-details{
        background: #e2e2e26e;
    }
    .app-details .details-top{
        width: 100%;
    }
    /* 顶部图片 */
    .app-details .details-top .details-top-img{
        width: 100%;
        background: #fff;
       
    }
     .app-details .details-top .details-top-img>img{
        width: 100%;
    }
    /* 标题 */
    .app-details .details-top>h4{
        text-align: center;
        padding-top:40px;
        color:#333333c7;
        background: #fff;
        margin: 0;
    }
    /* 分类名、发布时间 */
    .app-details .details-top .details-re{
        width: 100%;
        text-align: center;
        color:#777;
        padding-bottom:56px;
        background: #fff;
        padding-top:15px;
    }
    .app-details .details-top .details-re>p{
        margin-bottom:0;
        margin-top:5px; 

    }
    /* 方框 */
    .app-details .details-top .details-desc{
        width: 100%;
        padding:20px 0;
        background: #fff;
        margin-top: 13px;
        text-align:center;
    }
    .app-details .details-top .details-desc>p{
           padding:48px 0;
           min-width:85%;
           border:1px solid #333;
    }
    /* 方框下的内容 */
    .app-details .details-top .details-content p{
        background: #fff;
        margin-top:13px;
        color:#333;
        font-size:14px;
        padding:20px 0;
    }
    /* 页面中间内容 */
    .app-details .details-middle{
        width: 100%;
        background: #fff;
    }
    .app-details .details-middle h2{
        text-align: center;
        margin-top:13px;
        padding:22px 0;
        border-bottom:1px solid #e2e2e2;
        color:#333333c7;
    }
    .app-details .details-middle .middle-content{
        width: 100%;
    }
    .app-details .details-middle .middle-content p{
        color:#000;
    }
    /* 步骤数字 */
    .app-details .details-middle .middle-content .middle-cont-num{
        text-align: center;
        padding: 10px 0;
        color:#333;
    }
    .app-details .details-middle .middle-content img{
        width: 100%;
    }
    /* 页尾 */
    .app-details .details-bottom{
        width: 100%;
        height:55px;
        position:fixed;
        top:92%;
        left:0;
        line-height: 55px;
        background: #fff;
    }
    .mui-content{
        background: #fff !important;
    }
    .flex-container>span:first-child{
        float: left;
        
    }
    .flex-container>a:not(first-child){
        float: right;
    }
    .flex-container .bottom-right{
        float: right;  
    }
    /* 点击收藏颜色变亮 */
    .active {
        color: #f56600;
    }
</style>



